@import '../styles/variables';
@import '../styles/mixins';

@import "../styles/utils";
@import '../styles/layout';

@import '../styles/buttons';

html,
body {
  height: 100%;
}

html {
  box-sizing: border-box;
  font-size: $baseFontSize;
  font-family: $baseFontFam;
}

body {
  color: $textColorMain;
  background-color: $white;
  margin: 0;
  max-width: 100vw;
  font-size: 14px;
  font-family: 'Proxima Nova', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

/**
* Resets not included in Normalize.css
*/

img {
  max-width: 100%;
  vertical-align: middle;
}

/* Firefox doesn't wrap text in pre tags */
pre {
  white-space: pre-wrap;
  word-wrap: break-word;
}

/*
  The !important is important...fixes issue with ng-cloak not working in Bootstrap navbar type situations:
  Check the first comment under the documentation: http://docs.angularjs.org/api/ng.directive:ngCloak
*/
[ng\:cloak], [ng-cloak], .ng-cloak {
  display: none !important;
}




















.title-bar {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: $titleBarHeight;
  padding: 0 $md;
  background: $white;
  z-index: $zTitleBar;
}

.title-bar .app-title {
  color: $textColorMain;
  text-decoration: none;
}

.title-bar .wwt-logo {
  height: 29px;
}

.title-bar .wwt-logo,
.title-bar .app-name {
  vertical-align: middle;
}

.title-bar .app-name {
  @include font-smoothing();
  color: $grayMid;
  font-size: 18px;
}
