@import '../styles/variables';
@import '../styles/mixins';

@import "../styles/utils";
@import '../styles/layout';

@import '../styles/buttons';

html,
body {
  height: 100%;
}

html {
  box-sizing: border-box;
  font-size: $baseFontSize;
  font-family: $baseFontFam;
}

body {
  color: $textColorMain;
  background-color: $white;
  margin: 0;
  max-width: 100vw;
  font-size: 14px;
  font-family: 'Proxima Nova', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

/**
* Resets not included in Normalize.css
*/

img {
  max-width: 100%;
  vertical-align: middle;
}

/* Firefox doesn't wrap text in pre tags */
pre {
  white-space: pre-wrap;
  word-wrap: break-word;
}

/*
  The !important is important...fixes issue with ng-cloak not working in Bootstrap navbar type situations:
  Check the first comment under the documentation: http://docs.angularjs.org/api/ng.directive:ngCloak
*/
[ng\:cloak], [ng-cloak], .ng-cloak {
  display: none !important;
}

























.pmr-navbar {
  position: fixed;
  width: 100%;
  min-height: $navbarHeight;
  font-size: $md;
  background-color: $navbarColor;
  z-index: $zNavbar;
  top: $titleBarHeight;
  @extend .clearfix;
  display: flex;
  justify-content: flex-start;

  .nav-group,
  .nav-input {
    @extend .reset-list;
    display: flex;
    justify-content: flex-start;

    &.right {
      margin-left: auto;
    }
  }

  .nav-item {
    > a {
      transition: all 120ms ease;
      padding: 10px 25px;
      text-decoration: none;
      color: $white;
      display: flex;
      align-items: center;
      height: 100%;

      &:hover {
        background: rgba(0,0,0,.18);
      }

      &.active {
        background: rgba(0,0,0,.23);
        box-shadow: inset 0 0 2px 2px rgba(0,0,0,.15);
      }
    }
  }

  .nav-input {
    .input-addon {
      padding: 0 $sm;
      min-height: 43px;
      width: $lg;
      color: rgba(255,255,255,.7);
      background-color: rgba(0,0,0,.18);
      transition: all 120ms ease;
      order: -1;

      &:before {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
      }
    }

    input {
      order: 0;
      min-height: 43px;
      border: 0;
      color: $white;
      @include placeholder(rgba(255,255,255,.7));
      transition: all 120ms ease;
      background-color: rgba(0,0,0,.18);

      &:focus {
        background-color: rgba(0,0,0,.23);
        outline: none;

        & + .input-addon {
          color: $white;
          background-color: rgba(0,0,0,.23);
        }
      }
    }
  }

  &.with-icons {
    .nav-item {
      border-left: 1px solid $navbarBorderColor;
      border-right: 1px solid $navbarBorderColor;
      margin-left: -1px; // avoids double borders

      a {
        padding: 12px 25px;
        display: flex;
        flex-direction: column;
        align-items: stretch;
        justify-content: space-between;
        font-size: 12px;

        i {
          order: 0;
          padding-bottom: $xxs;
          text-align: center;
          font-size: 25px;
          color: $nearWhite;
        }
      }
    }

    .nav-input {
      padding: 13px 0;
      margin-right: 13px;
    }
  }
}

